















import Vue from 'vue';
import {mapState} from "vuex";
import {LOGOUT} from "@/store/actions.type";

export default Vue.extend({
    name: "Cabinet",

    computed: {
        ...mapState({
            user: (state: any) => state.auth.user
        })
    },

    methods: {
        logout() {
            this.$store.dispatch(LOGOUT);
            this.$router.push({ name: "home" });
        }
    }
});
